<div class="w-[100vw] md:w-[50vw] h-[70vh] md:h-[90vh] flex flex-col items-center justify-start bg-[#ffffff]"><div class="flex p-2 justify-center items-center"><input class="w-[50%] p-2 bg-white focus:outline-none focus:shadow-outline rounded-md py-2 px-2 block appearance-none leading-normal" v-model="guessNum" :class="[         errorMessage ? 'border border-red-500' : 'border border-gray-500',       ]" type="" placeholder="輸入要猜的數字"><button class="bg-blue-600 text-white ml-3 py-2 px-4 font-medium rounded-xl transition-all duration-300 hover:bg-blue-500" @click="action">送出</button><button class="bg-red-600 text-white ml-3 py-2 px-4 font-medium rounded-xl transition-all duration-300 hover:bg-red-500">提示</button></div><div class="text-base text-rose-600" v-if="errorMessage">{{errorMessage}}</div><div class="flex p-2 justify-center items-center"><div>數字</div><div>AB</div><div>提示</div></div><div v-for="item in data"><div>{{item}}</div></div></div>